import { auth } from '../config/firebase'

const API_URL = process.env.NEXT_PUBLIC_API_URL || process.env.NEXT_PUBLIC_CLOUDFLARE_API_URL || 'https://viaticos.davidzapata-dz051099.workers.dev'

async function getAuthToken() {
  if (typeof window === 'undefined') {
    throw new Error('getAuthToken solo puede ser llamado en el cliente')
  }
  if (!auth) {
    throw new Error('Firebase Auth no está disponible. Verifica las variables de entorno NEXT_PUBLIC_FIREBASE_*')
  }
  const user = auth.currentUser
  if (!user) {
    throw new Error('Usuario no autenticado')
  }
  const token = await user.getIdToken()
  return token
}

async function apiRequest(endpoint: string, options: RequestInit = {}) {
  const token = await getAuthToken()
  
  const response = await fetch(`${API_URL}${endpoint}`, {
    ...options,
    headers: {
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${token}`,
      ...options.headers,
    },
  })

  if (!response.ok) {
    const error = await response.json().catch(() => ({ message: 'Error en la solicitud' }))
    throw new Error(error.message || 'Error en la solicitud')
  }

  return response.json()
}

export async function uploadViatico(viaticoData: FormData) {
  const token = await getAuthToken()

  const response = await fetch(`${API_URL}/api/viaticos`, {
    method: 'POST',
    headers: {
      'Authorization': `Bearer ${token}`,
    },
    body: viaticoData,
  })

  if (!response.ok) {
    const error = await response.json().catch(() => ({ message: 'Error al subir viático' }))
    throw new Error(error.message || 'Error al subir viático')
  }

  return response.json()
}

